.characteristics-product-card__table {
  flex: 0 1 50%;
  border-bottom: 1px solid #dedede;
  border-right: 1px solid #dedede;
}
.characteristics-product-card__table:nth-child(even) {
  border-right: none;
}
.characteristics-product-card__table:nth-last-child(-n+2) {
  border-bottom: none;
}
.characteristics-product-card__item {
  padding: 5px 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.characteristics-product-card__left {
  flex: 0 1 60%;
  display: flex;
  align-items: center;
  margin: 0px 5px 0px 0px;
  font-weight: 300;
  font-size: 11px;
  line-height: 117%;
  letter-spacing: -0.03em;
  color: #000000;
}
.characteristics-product-card__icon {
  margin: 0px 10px 0px 0px;
  flex: 0 0 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.characteristics-product-card__right {
  flex: 0 1 40%;
  font-weight: 500;
  font-size: 11px;
  line-height: 117%;
  letter-spacing: -0.03em;
  text-align: right;
  color: #000000;
}

.bottom-product-card__content {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px 0px 20px 0px;
}
.bottom-product-card__text {
  font-weight: 600;
  font-size: 20px;
  line-height: 91%;
  letter-spacing: -0.03em;
  color: #ffffff;
}
.bottom-product-card__button {
  margin: 0px 0px 0px 48px;
}